@use'variaveis';

.footer {
    padding: 26px 8px;

    &__container{
        max-width: 1024px;
        width: 100%;
        margin: 0 auto;
        text-align: center;
    }

    &__logo{
        width: 80px;
        margin: 0 auto 16px;
        padding-bottom: 20px;
    }

    &__links{
        margin-bottom: 16px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;

        &__item{
            display: inline-block;
            
            a{
                text-decoration: none;
                padding: 8px 16px;

                &:hover{
                    color: variaveis.$corTextSecundario;
                }
            }
        }
        p{
            margin-bottom: 16px;
        }
    }
}

.language-selector {
    display: flex;
    align-items: center;
    gap: 8px;
    justify-content: center;

    img{
        height: 18px;
        width: 18px;
    }
    select{
        background-color: transparent;
        border: none;
        color: variaveis.$corTextPrincipal;
        height: 38px;

        &:hover{
            color: variaveis.$corTextSecundario;
        }

        option {
            color: variaveis.$corDeFundo;
        }
    }
}