.plans{
    padding: 5vw;

    h2{
        text-align: center;
    }

    &__list{
        display: flex;
        justify-content: space-around;
        // list-style: none;
        // padding: 0;
        margin-top: 50px;

        @media (max-width: 768px) {
            display: block;
        }

        .button{
            margin-top: 24px;
        }
        
        &__item{
            text-align: center;
            padding: 60px 44px 44px;
            max-width: 380px;
            width: 100%;
            
            &:nth-child(2){
                background-image: url(../images/fundo_combo_plus.png);
                background-repeat: no-repeat ;
                background-size: contain;
            }

            img{
                height: 83px;
            }

            strong{
                display: block;
                margin-top: 24px;
            }
        }
    }

    .text--small {
        text-align: center;
    }
}